import React, { useEffect, useRef, useState, useCallback } from 'react';
import { db } from './firebase';
import { collection, onSnapshot } from 'firebase/firestore';
import {
  Alert,
  Snackbar,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Typography,
  Box,
  Container,
  Button,
} from '@mui/material';
import './App.css';

function App() {
  const videoRef = useRef(null);
  const [isStreaming, setIsStreaming] = useState(false);
  const [isDetecting, setIsDetecting] = useState(false);
  const [notification, setNotification] = useState(null);
  const [events, setEvents] = useState([]);
  const wsRef = useRef(null);
  const pcRef = useRef(null);

  const startStream = async () => {
    if (isStreaming) return;

    wsRef.current = new WebSocket('ws://192.168.1.6:8000/ws');
    pcRef.current = new RTCPeerConnection();

    pcRef.current.ontrack = (event) => {
      if (event.streams[0]) {
        videoRef.current.srcObject = event.streams[0];
      }
    };

    pcRef.current.onicecandidate = (event) => {
      if (event.candidate) {
        wsRef.current.send(JSON.stringify({ candidate: event.candidate }));
      }
    };

    wsRef.current.onmessage = async (event) => {
      const data = JSON.parse(event.data);
      if (data.answer) {
        await pcRef.current.setRemoteDescription(new RTCSessionDescription(data.answer));
      } else if (data.candidate) {
        await pcRef.current.addIceCandidate(new RTCIceCandidate(data.candidate));
      } else if (data.violence_detected) {
        setNotification(
          `Violencia detectada a las ${data.violence_detected.timestamp}. IDs: ${data.violence_detected.ids_involved.join(", ")}`
        );
      }
    };

    wsRef.current.onopen = async () => {
      const offer = await pcRef.current.createOffer();
      await pcRef.current.setLocalDescription(offer);
      wsRef.current.send(JSON.stringify({ offer }));
    };

    wsRef.current.onerror = (error) => {
      console.error('Error en WebSocket:', error);
    };

    setIsStreaming(true);
  };

  const stopStream = useCallback(() => {
    if (!isStreaming) return;

    if (videoRef.current.srcObject) {
      videoRef.current.srcObject.getTracks().forEach(track => track.stop());
      videoRef.current.srcObject = null;
    }
    if (pcRef.current) {
      pcRef.current.close();
    }
    if (wsRef.current) {
      wsRef.current.close();
    }
    setIsStreaming(false);
  }, [isStreaming]);

  const handleStartDetection = () => {
    if (wsRef.current) {
      wsRef.current.send(JSON.stringify({ start_detection: true }));
      setIsDetecting(true);
    }
  };

  const handleStopDetection = () => {
    if (wsRef.current) {
      wsRef.current.send(JSON.stringify({ stop_detection: true }));
      setIsDetecting(false);
    }
  };

  useEffect(() => {
    // Escuchar eventos de Firestore
    const unsubscribe = onSnapshot(collection(db, "violence_events"), (snapshot) => {
      const eventList = snapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() }));
      setEvents(eventList);
    });

    return () => {
      stopStream();
      unsubscribe();
    };
  }, [stopStream]);

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Typography variant="h4" gutterBottom align="center">
        Deteccin de Violencia Fsica Escolar en Tiempo Real
      </Typography>

      <Typography variant="body1" align="center" sx={{ mb: 2 }}>
        Deteccin: {isDetecting ? "Activa" : "Inactiva"}
      </Typography>

      <Box sx={{ mb: 4, display: "flex", justifyContent: "center" }}>
        <Box
          sx={{
            border: "2px solid #ccc",
            borderRadius: "8px",
            overflow: "hidden",
            width: "1280px",
            height: "720px",
          }}
        >
          <video
            ref={videoRef}
            autoPlay
            playsInline
            style={{ width: "100%", height: "100%" }}
          />
        </Box>
      </Box>

      <Box sx={{ mb: 2, display: "flex", justifyContent: "center", gap: 2 }}>
        <Button
          variant="contained"
          color="primary"
          onClick={() => {
            startStream();
            handleStartDetection();
          }}
          disabled={isStreaming}
        >
          Iniciar Stream y Deteccin
        </Button>
        <Button
          variant="contained"
          color="secondary"
          onClick={() => {
            stopStream();
            handleStopDetection();
          }}
          disabled={!isStreaming}
        >
          Detener Stream y Deteccin
        </Button>
      </Box>

      <Typography variant="h5" gutterBottom>
        Historial de Eventos de Violencia
      </Typography>
      <TableContainer component={Paper} sx={{ mb: 4 }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell sx={{ fontWeight: "bold" }}>Timestamp</TableCell>
              <TableCell sx={{ fontWeight: "bold" }}>Frames</TableCell>
              <TableCell sx={{ fontWeight: "bold" }}>Probabilidad</TableCell>
              <TableCell sx={{ fontWeight: "bold" }}>IDs Involucrados</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {events.length > 0 ? (
              events.map((event) => (
                <TableRow key={event.id}>
                  <TableCell>{event.timestamp || "N/A"}</TableCell>
                  <TableCell>{event.start_frame && event.end_frame ? `${event.start_frame}-${event.end_frame}` : "N/A"}</TableCell>
                  <TableCell>{event.probability ? event.probability.toFixed(4) : "N/A"}</TableCell>
                  <TableCell>{event.ids_involved?.join(", ") || "N/A"}</TableCell>
                </TableRow>
              ))
            ) : (
              <TableRow>
                <TableCell colSpan={4} align="center">
                  No hay eventos registrados.
                </TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </TableContainer>

      <Snackbar
        open={!!notification}
        autoHideDuration={6000}
        onClose={() => setNotification(null)}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert onClose={() => setNotification(null)} severity="error" sx={{ width: "100%" }}>
          {notification}
        </Alert>
      </Snackbar>
    </Container>
  );
}

export default App;